import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { BaseModel } from '../../models/base.model';
import { BaseService } from './base.service';

@Injectable()
export class LoginBaseService<T extends BaseModel> extends BaseService<T> {

    constructor(
        @Inject(HttpClient) protected http: HttpClient,
        @Inject('url') protected url: string,
        @Inject('endpoint') protected endpoint: string,
    ) {
        super(
            http, url, endpoint
        );

    }
    public login(credentials, headers?): Observable<any> {
        return this.http.post<any>(`${this.url}/${this.endpoint}/login`, credentials, this.httpHeadersWithoutAuth(headers))
            .pipe(
                map((data) => data)
            );
    }

    public passwordRecovery(email, headers?): Observable<any> {
        return this.http.post<any>(`${this.url}/${this.endpoint}/password-recovery`, email, this.httpHeadersWithoutAuth(headers))
            .pipe(
                map((data) => data)
            );
    }
    public resetPassword(newPassword, hash, headers?): Observable<any> {
        return this.http.post<any>(`${this.url}/${this.endpoint}/reset-password`,
            { 'newPassword': newPassword, 'hash': hash }, this.httpHeadersWithoutAuth(headers))
            .pipe(
                map((data) => data)
            );
    }

    public getCurrentUser(token, headers?): Observable<T> {
        return this.http.get(this.url + '/users-me', this.createHttpHeaders(headers))
        .pipe(map((data: any) => data));
    }

    public register(item: T, headers?): Observable<T> {
        return this.http.post<any>(`${this.url}/${this.endpoint}/register`, item , this.httpHeadersWithoutAuth(headers))
            .pipe(
                map((data: any) => data)
            );
    }

}
