import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Injectable, Inject } from '@angular/core';
import { BaseModel } from '../../models/base.model';
import { HttpHeaders, HttpClient, HttpErrorResponse } from '@angular/common/http';


@Injectable()
export abstract class BaseService<T extends BaseModel>  {
    httpOptions = {
        headers: new HttpHeaders({
            // tslint:disable-next-line:max-line-length
            'Accept': '*',
            'Content-type': 'application/json',
        })
    };
    constructor(
        @Inject(HttpClient) protected http: HttpClient,
        @Inject('url') protected url: string,
        @Inject('endpoint') protected endpoint: string,

        // private url: string,
    ) {

    }

    public getAll(headers?): Observable<T[]> {
        return this.http.get(`${this.url}/${this.endpoint}`, this.createHttpHeaders(headers))
            .pipe(
                map((data: T[]) => data),
            )
            ;
    }

    public getById(_id: string, headers?): Observable<T> {
        return this.http.get(`${this.url}/${this.endpoint}/${_id}`, this.createHttpHeaders(headers))
            .pipe(
                map((data: T) => data),
            );
    }

    public create(item: T, headers?): Observable<T> {
        return this.http.post<T>(`${this.url}/${this.endpoint}`, item, this.createHttpHeaders(headers))
            .pipe(
                map((data: T) => data)
            );
    }

    public update(_id: string, item: T, headers?): Observable<T> {
        item._id = _id;
        return this.http.patch<T>(`${this.url}/${this.endpoint}`, item, this.createHttpHeaders(headers))
            .pipe(
                map((data: T) => data),
            );
    }

    public updateComplete(_id: string, item: T, headers?): Observable<T> {
        item._id = _id;
        return this.http.put<T>(`${this.url}/${this.endpoint}`, item, this.createHttpHeaders(headers))
            .pipe(
                map((data: T) => data),
            );
    }

    public delete(_id: string, headers?): Observable<any> {
        return this.http.delete<T>(`${this.url}/${this.endpoint}/${_id}`, this.createHttpHeaders(headers))
            .pipe(
                map((data: T) => data)
            );
    }

    public createHttpHeaders(headers?) {
        const httpOptions = Object.assign({}, this.httpOptions);
        if (headers) {
            Object.keys(headers).forEach((headerKey) => {
                httpOptions.headers = httpOptions.headers.append(headerKey, headers[headerKey]);
            });
        }
        return httpOptions;
    }

    public httpHeadersWithoutAuth(headers?) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Accept': '*',
                'Content-type': 'application/json',
                'X-Skip-Interceptor': ''
            })
        };

        if (headers) {
            Object.keys(headers).forEach((headerKey) => {
                httpOptions.headers = httpOptions.headers.append(headerKey, headers[headerKey]);
            });
        }
        return httpOptions;
    }
}
